import React from "react";
import { experiences } from "./data";
import Resp from "./responsibilties/Resp";
import "./Exp.css";
const Exp = () => {
  return (
    <div className="exp" id="Experience">
      <div className="exp_outer_wrap">
        <h1>Experience</h1>
        <div className="exp_wrap">
          {experiences.map((item) => {
            const { job_place, job_title, time, resp, exp_title } = item;
            return (
              <div className="card" key={exp_title}>
                <h4 className="title">{exp_title}</h4>
                <span className="time">{time}</span>
                {job_title && <p className="job_title">{job_title}</p>}
                {job_place && <p className="job_place">{job_place}</p>}

                {resp && <Resp resp={resp} />}
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Exp;
