import React, { useState } from "react";
import { proj } from "./data";
import "./Projects.css";
const Projects = () => {
  const [projects, setProjects] = useState(proj);
  const buttons = ["All", ...new Set(proj.map((p) => p.coding_lang))];
  const [i, setI] = useState(0);
  const clickHandler = (name, index) => {
    setI(index);
    if (name === "All") {
      setProjects(proj);
    } else {
      let filtered = proj.filter((item) => item.coding_lang == name);
      setProjects(filtered);
    }
  };
  return (
    <div id="Projects">
      <h1>Projects</h1>
      <div className="button_selects">
        {buttons.map((btn, index) => {
          return (
            <button
              className={i === index ? "btn active" : "btn"}
              key={index}
              onClick={() => clickHandler(btn, index)}
            >
              {btn}
            </button>
          );
        })}
      </div>
      <div className="all_projects">
        {projects.map((item) => {
          const { title, about, code_link, site_link, coding_lang, img } = item;
          return (
            <div className="project_card" key={title}>
              <img src={img} alt={title} />
              <div className="info">
                <p className="title">{title}</p>
                <span>{coding_lang}</span>
                <p className="about">{about}</p>
                <div className="b_wrap">
                  {code_link && (
                    <a className="code_link" href={code_link}>
                      Check the code
                    </a>
                  )}
                  <a className="site_link" href={site_link}>
                    Check it out
                  </a>
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Projects;
