import React from "react";
import me from "./me.jpg";
import "./PageTop.css";
const PageTop = () => {
  return (
    <div className="banner" id="About me">
      <div className="text_box">
        <img src={me} alt="Me" />
        <p>Hi, my name is Alexandr Shobotenko</p>
        <p>
          And I'm aspiring to become a <span>Front-end Developer</span>
        </p>
      </div>
    </div>
  );
};

export default PageTop;
