import React from "react";
import "./Skills.css";
import { skills } from "./data";
const Skills = () => {
  return (
    <div id="Skills">
      <h1>Skills</h1>
      <div className="all_skills">
        {skills.map((item, index) => {
          const { ext_title, title, short_desc, color } = item;
          return (
            <div
              className="skill"
              key={index}
              style={{
                background: `linear-gradient(145deg, rgba(255,253,228,1) 0%, ${color} 100%)`,
              }}
            >
              {/* <h2 style={{ boxShadow: `inset 0px 0px 10px ${color}` }}>
                {title}
              </h2> */}
              <div className="ext_title">{ext_title}</div>
              <div className="short_description">{short_desc}</div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Skills;
