import React, { useEffect, useRef } from "react";
import { FaGithub, FaTelegram, FaLinkedin } from "react-icons/fa";
import { page_links } from "./data";
import "./Header.css";
import AnchorLink from "react-anchor-link-smooth-scroll";
const Header = () => {
  const linksRef = useRef(null);
  const burgerRef = useRef(null);
  const headerRef = useRef(null);
  const showLinks = () => {
    linksRef.current.classList.toggle("visible");
    burgerRef.current.classList.toggle("active");
  };
  const hideLinks = () => {
    linksRef.current.classList.remove("visible");
    burgerRef.current.classList.remove("active");
  };
  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.pageYOffset >= 100) {
        headerRef.current.classList.add("scrolled");
      } else if (window.pageYOffset <= 99) {
        headerRef.current.classList.remove("scrolled");
      }
      return () => {
        window.removeEventListener("scroll");
      };
    });
  });
  return (
    <header ref={headerRef}>
      <div className="wrap">
        <div className="socials">
          <a href="https://telegram.me/Al_ex_Ash">
            <FaTelegram />
          </a>
          <a href="https://github.com/Sane404">
            <FaGithub />
          </a>
          <a href="https://www.linkedin.com/in/alexandr-shobotenko-354b37206/">
            <FaLinkedin />
          </a>
        </div>
        <div className="page_links" ref={linksRef}>
          {page_links.map((link, index) => {
            return (
              <AnchorLink
                offset="100"
                href={`#${link}`}
                key={index}
                onClick={hideLinks}
              >
                {link}
              </AnchorLink>
              // <a href="#" key={index} onClick={hideLinks}>
              //   {link}
              // </a>
            );
          })}
        </div>
        <div className="burger" onClick={showLinks} ref={burgerRef}>
          <div className="line line1"></div>
          <div className="line line2"></div>
          <div className="line line3"></div>
        </div>
      </div>
    </header>
  );
};

export default Header;
